@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
*,
*::before,
*::after {
  box-sizing: border-box;
}

:root {
  --ff-primary: 'Poppins', sans-serif;
  --ff-secondary: 'Poppins', sans-serif;

  --fw-reg: 300;
  --fw-bold: 900;

  --clr-light: #6fa2d9;
  --clr-medium: #2a6db5;
  --clr-dark: #000000;
  --clr-accent: #fffafa;

  --fs-h1: 2rem;
  --fs-h2: 1.5rem;
  --fs-h3: 1.25rem;
  --fs-h4: 1rem;
  --fs-body: 0.8rem;

  --bs: 0.25em 0.25em 0.75em rgba(0, 0, 0, 0.25),
    0.125em 0.125em 0.25em rgba(0, 0, 0, 0.15);
}

@media (min-width: 800px) {
  :root {
    --fs-h1: 4rem;
    --fs-h2: 3.75rem;
    --fs-h3: 1.5rem;
    --fs-h4: 1.3rem;
    --fs-body: 1.125rem;
  }
}

@media (min-width: 500px) {
  :root {
    --fs-h1: 3rem;
    --fs-h2: 2.75rem;
    --fs-h3: 1.5rem;
    --fs-h4: 1.25rem;
    --fs-body: 1rem;
  }
}

/* General styles */

html {
  scroll-behavior: smooth;
}

body {
  /* background-image: url('./assets/records/collage.jpeg');
  background-size: cover;
  background-repeat: no-repeat; */
  background: var(--clr-dark);
  color: var(--clr-dark);
  margin: 0;
  font-family: var(--ff-secondary);
  font-size: var(--fs-body);
  line-height: 1.6;
  z-index: 0;
  height: 100%;
}

section {
  padding: 5em 2em;
}

img {
  display: block;
  max-width: 100%;
}

strong {
  font-weight: var(--fw-bold);
}

:focus {
  outline: 3px solid var(--clr-accent);
  outline-offset: 3px;
}

/* Buttons */

.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 110px;
  background: var(--clr-accent);
  color: var(--clr-dark);
  padding: 10px;
  text-align: center;
  font-size: 16px;
  cursor: pointer;
  border: 1px solid var(--clr-dark);
  text-decoration: none;
  transition: 200ms;
  font-weight: 700;
  letter-spacing: 1px;
  box-shadow: var(--bs);
}

.btn:hover {
  background: var(--clr-dark);
  color: var(--clr-accent);
}

/* Typography */

h1,
h2,
h3,
h4 {
  line-height: 1.1;
  margin: 0;
  font-family: var(--ff-primary);
}

h1 {
  font-size: var(--fs-h1);
}

h2 {
  font-size: var(--fs-h2);
}

h3 {
  font-size: var(--fs-h3);
}

h4 {
  font-size: var(--fs-h4);
}

p {
  font-size: 1.1em;
  line-height: 2em;
}

ul,
ol {
  line-height: 2em;
}

li {
  padding-left: 10px;
  margin-bottom: 1em;
}

/* Transform effects */

.slide-in-left-enter {
  transform: translateX(-100%);
}

.slide-in-left-enter-active {
  transform: translateX(0);
  opacity: 1;
  transition: all 200ms;
}

.slide-in-left-exit {
  transform: translateX(0%);
  opacity: 1;
}

.slide-in-left-exit-active {
  transform: translateX(-100%);
  opacity: 0;
  transition: all 200ms;
}

.main-header {
  width: 100%;
  height: 3.5rem;
  display: flex;
  align-items: center;
  align-content: center;
  justify-content: space-between;
  position: fixed;
  top: 0;
  left: 0;
  background: var(--clr-dark);
  padding: 0 1rem;
  box-shadow: var(--bs);
  z-index: 5;
  opacity: 0.9;
}

main {
  margin-top: 5rem;
}

@media (min-width: 768px) {
  .main-header {
    justify-content: space-between;
  }
}
.nav-links {
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.nav-links li {
  display: table-cell;
  position: relative;
  padding: 10px 0;
}

.nav-links ul {
  margin: 150px auto 0;
  padding: 0;
  list-style: none;
  display: table;
  width: 600px;
  text-align: center;
}

/* ✅ Apply the same styles to both <a> and <button> */
.nav-links a,
.nav-links button {
  color: var(--clr-accent);
  text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 0.19em;
  display: inline-block;
  padding: 12px 15px;
  position: relative;
  background: none;
  border: none;
  cursor: pointer;
  font-size: inherit;
  font-family: inherit;
}

.nav-links a:active,
.nav-links a:focus,
.nav-links button:active,
.nav-links button:focus {
  outline: 0;
  border: none;
  color: var(--clr-medium);
}

.nav-links a:hover,
.nav-links button:hover {
  color: var(--clr-medium);
}

/* ✅ Ensure both <a> and <button> get the underline effect */
.nav-links a::after,
.nav-links button::after {
  background: none repeat scroll 0 0 transparent;
  bottom: 0;
  content: '';
  display: block;
  height: 3px;
  left: 50%;
  position: absolute;
  background: var(--clr-medium);
  transition: width 0.5s ease 0s, left 0.5s ease 0s;
  width: 0;
}

.nav-links a:hover::after,
.nav-links button:hover::after {
  width: 100%;
  left: 0;
}

@media (min-width: 1000px) {
  .nav-links ul {
    margin-top: 40px;
  }

  .nav-links {
    flex-direction: row;
  }

  .nav-links li {
    margin: 0 0.5rem;
  }
}

.side-drawer {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 1000;
  height: 100vh;
  width: 70%;
  background: var(--clr-dark);
  box-shadow: var(--br);

  /* ✅ Use Flexbox to Center Content */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

/* ✅ Add More Space Between NavLinks ONLY in SideDrawer */
.side-drawer .nav-links li {
  margin: 20px 0; /* Increase vertical spacing */
}

.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.75);
  z-index: 10;
}
.modal {
  z-index: 100;
  position: fixed;
  top: 10vh;
  left: 10%;
  width: 80%;
  background: var(--clr-medium);
  box-shadow: var(--bs);
  border-radius: 8px;
  padding: 1rem;
}

/* Header */
.modal__header {
  width: 100%;
  padding: 0.75rem;
  background: var(--clr-dark);
  color: var(--clr-accent);
  letter-spacing: 0.05em;
  text-align: center;
  border-radius: 8px 8px 0 0;
}

.modal__header h2 {
  margin: 0;
  font-size: 1.4rem;
}

/* Content */
.modal__content {
  padding: 1rem;
  letter-spacing: 0.05em;
  color: var(--clr-accent);
}

/* List styles for better readability */
.modal__content ul {
  padding-left: 1.2rem;
  list-style: none;
}

.modal__content li {
  margin-bottom: 0.75rem;
  font-size: 1rem;
}

/* Link styling */
.modal__content a {
  color: var(--clr-light);
  text-decoration: none;
  font-weight: bold;
  transition: color 0.3s ease;
}

.modal__content a:hover {
  color: var(--clr-accent);
  text-decoration: underline;
}

/* Footer */
.modal__footer {
  padding: 1rem;
  font-style: italic;
  text-align: center;
  border-top: 1px solid var(--clr-dark);
}

/* Button */
.modal__footer .btn {
  background: var(--clr-dark);
  color: var(--clr-accent);
  border: none;
  padding: 0.75rem 1.5rem;
  font-size: 1rem;
  letter-spacing: 0.05em;
  cursor: pointer;
  border-radius: 4px;
  transition: background 0.3s ease, color 0.3s ease;
  width: 100%;
}

.modal__footer .btn:hover {
  background: var(--clr-accent);
  color: var(--clr-dark);
}

/* Responsive Adjustments */
@media (min-width: 600px) {
  .modal {
    left: calc(50% - 15rem);
    width: 30rem;
  }
}

/* Modal Animations */
.modal-enter {
  transform: translateY(-10rem);
  opacity: 0;
}

.modal-enter-active {
  transform: translateY(0);
  opacity: 1;
  transition: all 200ms ease-in-out;
}

.modal-exit {
  transform: translateY(0);
  opacity: 1;
}

.modal-exit-active {
  transform: translateY(-10rem);
  opacity: 0;
  transition: all 200ms ease-in-out;
}

