/* Footer stays at the bottom */
.footer {
  background: var(--clr-dark);
  color: var(--clr-accent);
  text-align: center;
  padding-top: 0; /* Reduced padding at the top of the footer */
  padding-bottom: 0.5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; /* Ensure everything is centered */
  width: 100%;
  position: fixed; /* Fix it at the bottom */
  bottom: 0;
  left: 0;
}

/* Footer links */
.footer a {
  color: inherit;
  text-decoration: none;
  border: none;
}

/* Social links container */
.social-list {
  list-style: none;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 0.3em; /* Keeps links and copyright closer */
